@import url("https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css");
@import url('https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Rubik:500,700,900&display=swap');
body {
    margin: 0;
    line-height: 1.5;
    font-size: 16px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400; }
* {
    margin: 0;
    padding: 0;
    outline: none;
    text-decoration: none;
    box-sizing: border-box; }
::before,::after {
    box-sizing: border-box; }
input,
textarea,
select {
    font-family: 'Montserrat', sans-serif; }

.section {
    background-color: #f2f2fc;
    min-height: 100vh;
    display: block;
    padding: 0 30px;
    position: fixed;
    left: 270px;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    opacity: 1;
    &.active {
        opacity: 1;
        z-index: 2; }

    .container {
        padding-top: 60px;
        padding-bottom: 70px; } }

.section-title {
    flex: 0 0 100%;
    -ms-flex: 0 0 100%;
    max-width: 100%;
    margin-bottom: 60px;
    h2 {
        font-size: 40px;
        color: #302e43;
        font-family: 'Rubik', sans-serif;
        font-weight: 700;
        margin: 0;
        position: relative;
        &::before {
            content: '';
            height: 4px;
            width: 50px;
            position: absolute;
            top: 100%;
            left: 0; }
        &::after {
            content: '';
            height: 4px;
            width: 25px;
            position: absolute;
            top: 100%;
            left: 0;
            margin-top: 8px; } } }

.row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
    position: relative; }

.padd-15 {
    padding-right: 15px;
    padding-left: 15px; }

.container {
    max-width: 1170px;
    width: 100%;
    margin: auto; }
.shoadow-dark {
    -webkit-box-shadow: 0 0 20px rgba(48,46,77,0.15);
    box-shadow: 0 0 20px rgba(48,46,77,0.15); }
.btn {
    font-size: 16px;
    font-family: 'Rubik', sans-serif;
    font-weight: 500;
    padding: 12px 35px;
    color: #ffffff;
    border-radius: 40px;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    display: inline-block;
    white-space: nowrap;
    border: none;
    cursor: pointer;
    &:hover {
        -webkit-transform: scale(1.05);
        transform: scale(1.05); } }
.hidden {
    display: none!important; }
// Aside
.aside {
    width: 270px;
    background-color: #fdf9ff;
    position: fixed;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 10;
    border-right: 1px solid #e8dfec;
    padding: 30px;
    & .logo {
        padding: 30px 0;
        & a {
            font-size: 40px;
            color: #302e43;
            font-family: 'Rubik', sans-serif;
            font-weight: 700;
            text-decoration: none;
            display: inline-block;
            position: relative;
            padding: 0 10px;
            &::before {
                content: '';
                height: 20px;
                width: 20px;
                position: absolute;
                top: 0;
                left: 0; }

            &::after {
                content: '';
                height: 20px;
                width: 20px;
                position: absolute;
                bottom: 0;
                right: 0; } } }
    & .nav-toggler {
        height: 40px;
        width: 45px;
        border: 1px solid #d4d4e3;
        cursor: pointer;
        position: fixed;
        left: 300px;
        top: 30px;
        z-index: 11;
        border-radius: 5px;
        background-color: #fdf9ff;
        // display: -ms-flexbox
        // display: flex
        display: none;
        -ms-flex-align: center;
        align-items: center;
        -ms-flex-pack: center;
        justify-content: center;
        & span {
            height: 2px;
            width: 18px;
            display: inline-block;
            position: relative; }
        & span::before {
            content: '';
            height: 2px;
            width: 18px;
            position: absolute;
            top: -6px;
            left: 0; }
        & span::after {
            content: '';
            height: 2px;
            width: 18px;
            position: absolute;
            top: 6px;
            left: 0; } }

    & .nav {
        list-style: none;
        margin: 80px 0;
        padding: 0;
        & li {
            display: block;
            & a {
                font-size: 26px;
                font-weight: 600;
                color: #302e4d;
                text-decoration: none;
                line-height: 70px;
                display: block;
                border-bottom: 1px solid #e8dfec;
                -webkit-transition: all 0.3s ease;
                transition: all 0.3s ease;
                text-transform: capitalize;
                & .fa {
                    margin-right: 10px; } }
            &:not(.active):hover {
                padding-left: 5px; } } }

    & .copyright-text {
        background-color: #ededed;
        font-size: 13px;
        color: #7d7d7d; } }

// Home 41.37

.home {
    min-height: 100vh;
    display: -ms-flexbox;
    display: flex;
    .intro {
        text-align: center;
        img {
            height: 200px;
            width: 200px;
            border-radius: 50%;
            display: inline-block;
            border: 8px solid #ffffff; }
        h1 {
            font-size: 30px;
            color: #302e43;
            font-family: 'Rubik', sans-serif;
            font-weight: 700;
            margin: 20px 0 5px; }
        p {
            font-size: 16px;
            font-weight: 500;
            margin: 0;
            line-height: 22px;
            color: #504e70; }
        .social-links {
            margin-top: 25px;
            a {
                height: 35px;
                width: 35px;
                display: inline-block;
                text-align: center;
                line-height: 35px;
                color: #ffffff;
                margin: 0 4px;
                border-radius: 50%;
                text-decoration: none;
                -webkit-transition: all 0.3s ease;
                transition: all 0.3s ease; }
            a:hover {
                -webkit-transform: scale(1.1);
                transform: scale(1.1); } } } }

// About Me   56.25
.about {
    & .about-content {
        -ms-flex: 0 0 100%;
        flex: 0 0 100%;
        max-width: 100%;
        & .timeline-box {
            -ms-flex: 0 0 100%;
            flex: 0 0 100%;
            max-width: 100%; }

        & .timeline {
            background-color: #fdf9ff;
            padding: 30px 15px;
            border: 1px solid #d4d4e3;
            border-radius: 10px;
            width: 100%;
            position: relative;
            & .timeline-item {
                position: relative;
                padding-left: 37px;
                padding-bottom: 50px;
                &:last-child {
                    padding-bottom: 0; } }
            & .timeline-item::before {
                content: '';
                width: 1px;
                height: 100%;
                position: absolute;
                left: 7px;
                top: 0; }
            & .circle-dot {
                position: absolute;
                left: 0;
                top: 0;
                height: 15px;
                width: 15px;
                left: 0;
                border-radius: 50px; }
            & .timeline-date {
                font-weight: 400;
                font-size: 14px;
                margin-bottom: 12px;
                color: #504e70;
                & .fa {
                    margin-right: 5px; } }
            & .timeline-title {
                font-weight: 700;
                font-size: 18px;
                margin-bottom: 15px;
                text-transform: capitalize;
                color: #302e43; }
            & .timeline-text {
                line-height: 25px;
                font-size: 16px;
                margin: 0;
                color: #504e70; } }

        & h3 {
            &.title {
                font-size: 24px;
                margin-bottom: 30px;
                font-weight: 700;
                color: #302e4d; } }

        & .education {
            -ms-flex: 0 0 50%;
            flex: 0 0 50%;
            max-width: 50%;
            margin-top: 30px; }
        & .experience {
            -ms-flex: 0 0 50%;
            flex: 0 0 50%;
            max-width: 50%;
            margin-top: 30px; }
        & .about-text {
            -ms-flex: 0 0 100%;
            flex: 0 0 100%;
            max-width: 100%; }
        & .personal-info {
            // background-color: red
            -ms-flex: 0 0 60%;
            flex: 0 0 60%;
            max-width: 60%;
            margin-top: 40px;
            & .info-item {
                -ms-flex: 0 0 50%;
                flex: 0 0 50%;
                max-width: 50%;
                & p {
                    font-weight: 600;
                    font-size: 16px;
                    padding: 20px 0;
                    color: #302e43;
                    margin: 0;
                    border-bottom: 1px solid #e8dfec;
                    & span {
                        font-weight: 400;
                        color: #504e70;
                        margin-left: 4px;
                        display: inline-block; } } } }
        & .buttons {
            margin-top: 30px;
            & .btn {
                margin-right: 15px;
                margin-top: 10px; } }

        & .skills {
            // background-color: blue
            -ms-flex: 0 0 40%;
            flex: 0 0 40%;
            max-width: 40%;
            margin-top: 40px;
            & h3 {
                font-size: 24px;
                margin-bottom: 15px;
                font-weight: 700;
                color: #302e4d; }
            & p {
                font-size: 16px;
                line-height: 25px;
                color: #504e70;
                margin: 0; }
            & .skill-item {
                -ms-flex: 0 0 100%;
                flex: 0 0 100%;
                max-width: 100%;
                margin-bottom: 30px;

                h5 {
                    line-height: 40px;
                    font-weight: 600;
                    font-size: 16px;
                    color: #302e43;
                    text-transform: capitalize;
                    margin: 0; }
                & .progress {
                    background-color: #e3e3e3;
                    height: 7px;
                    border-radius: 4px;
                    width: 100%;
                    position: relative;
                    & .progress-in {
                        position: absolute;
                        left: 0;
                        top: 0;
                        height: 100%;
                        border-radius: 4px; } }

                & .skill-percent {
                    // display: none 46.32
                    line-height: 40px;
                    position: absolute;
                    right: 0;
                    top: -40px;
                    font-weight: 400;
                    color: #302e43; } } } }
 }    // portfolio Section
.portfolio {
    & .container {
        padding-bottom: 40px; }
    & .portfolio-item-inner {
        border: 6px solid #fdf9ff;
        border-radius: 10px;
        overflow: hidden;
        cursor: pointer;
        position: relative;
        &:hover .portfolio-info .icon {
            opacity: 1;
            -webkit-transform: translateX(0px);
            transform: translateX(0px); }
        &:hover .portfolio-info h4 {
            opacity: 1;
            -webkit-transform: translateX(0px);
            transform: translateX(0px); }
        &:hover .portfolio-info {
            opacity: 1; }
        & .portfolio-img img {
            width: 100%;
            display: block; } }
    & .portfolio-filter {
        // background-color: red 20.13
        -ms-flex: 0 0 100%;
        flex: 0 0 100%;
        max-width: 100%;
        text-align: center;
        margin-bottom: 40px;
        & button {
            border: none;
            background-color: transparent;
            font-size: 18px;
            font-weight: 700;
            text-transform: capitalize;
            margin: 0 50px;
            color: #302e43;
            cursor: pointer;
            border-bottom: 2px solid transparent;
            white-space: nowrap;
            padding-bottom: 2px;
            -webkit-transition: all 0.3s ease;
            transition: all 0.3s ease; } }
    & .portfolio-item {
        margin-bottom: 30px;
        flex: 0 0 33.33%;
        max-width: 33.33%;
        &.hide {
            display: none; }
        &.show {
            display: block;
            animation: showItem 0.5s ease; }
        & .portfolio-info {
            position: absolute;
            left: 0;
            top: 0;
            height: 100%;
            width: 100%;
            background-color: rgba(0,0,0,0.6 );
            z-index: 1;
            padding: 30px;
            -webkit-transition: all 0.3s ease;
            transition: all 0.3s ease;
            opacity: 0;
            & h4 {
                font-size: 18px;
                font-weight: 700;
                color: #ffffff;
                text-transform: capitalize;
                -webkit-transition: all 0.3s ease;
                transition: all 0.3s ease;
                -webkit-transform: translateX(-20px);
                transform: translateX(-20px);
                opacity: 0; }
            & .icon {
                width: 40px;
                height: 40px;
                background-color: #ffffff;
                text-align: center;
                border-radius: 50%;
                position: absolute;
                right: 30px;
                bottom: 30px;
                -webkit-transition: all 0.3s ease;
                transition: all 0.3s ease;
                -webkit-transform: translateX(20px);
                transform: translateX(20px);
                opacity: 0;
                & .fa {
                    line-height: 40px; } } } } }

// Lightbox 48.25

.lightbox {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.6);
    z-index: 100;
    display: none;
    justify-content: center;
    align-items: center;
    cursor: zoom-out;
    padding: 30px;
    &.open {
        display: flex;
        & .lightbox-content {
            animation: lightboxImage .5s ease; } }

    & .lightbox-controls {
        & .prev-item {
            position: absolute;
            height: 40px;
            width: 40px;
            background-color: #ffffff;
            border-radius: 50%;
            text-align: center;
            cursor: pointer;
            top: 50%;
            margin-top: -20px;
            z-index: 110;
            transition: all 0.3s ease;
            left: 30px;
            &:hover {
                transform: scale(1.2); }
            & .fa {
                font-size: 32px;
                line-height: 40px; } }
        & .next-item {
            position: absolute;
            height: 40px;
            width: 40px;
            background-color: #ffffff;
            border-radius: 50%;
            text-align: center;
            cursor: pointer;
            top: 50%;
            margin-top: -20px;
            z-index: 110;
            transition: all 0.3s ease;
            right: 30px;
            &:hover {
                transform: scale(1.2); }
            & .fa {
                font-size: 32px;
                line-height: 40px; } } }

    & .lightbox-content {
        position: relative;

        & .lightbox-close {
            position: absolute;
            height: 40;
            width: 40;
            top: 0;
            right: 0;
            font-size: 16px;
            color: #ffffff;
            text-align: right;
            line-height: 40px; }
        & img {
            height: auto;
            width: auto;
            max-width: 100%;
            max-height: 500px;
            cursor: pointer;
            display: block;
            padding: 40px 0 30px; }
        & .lightbox-caption {
            position: absolute;
            bottom: 0;
            width: 100%;
            left: 0;
            display: flex;
            justify-content: space-between;
            font-size: 16px;
            font-weight: 400;
            color: #ffffff;
            z-index: -1; } } }
@keyframes lightboxImage {
    0% {
        transform: scale(0.8); }
    100% {
        transform: scale(1); } }
@keyframes showItem {
    0% {
        transform: scale(0.8);
        opacity: 0; }
    100% {
        transform: scale(1);
        opacity: 1; } }

// Contact

.contact {
    & .contact-form {
        flex: 0 0 100%;
        max-width: 100%;
        & .btn {
            height: 50px;
            padding: 0 50px; }
        & .col-6 {
            flex: 0 0 50%;
            max-width: 50%;
 }            // background-color: red
        & .col-12 {
            // background-color: red
            flex: 0 0 100%;
            max-width: 100%; }
        & .form-item {
            margin-bottom: 30px;
            & textarea.form-control {
                height: 140px; }
            & .form-control {
                width: 100%;
                height: 50px;
                border-radius: 25px;
                border: 1px solid #d4d4e3;
                padding: 10px 25px;
                font-size: 16px;
                color: #504e70;
                transition: all 0.3s ease;
                &:focus {
                    box-shadow: 0 0 20px rgba(48,46,77,0.15); } } } }
    & .contact-info-item {
        flex: 0 50%;
        max-width: 50%;
        text-align: center;
        margin-bottom: 40px;
        & .icon {
            display: inline-block;
            & .fa {
                font-size: 40px; } }
        & h4 {
            font-size: 18px;
            font-weight: 700;
            color: #302e4d;
            text-transform: capitalize;
            margin: 15px 0 5px; }

        & p {
            font-size: 16px;
            line-height: 25px;
            color: #504e70;
            margin: 0;
            font-weight: 400; } } }
// Dark

body.dark .section {
    background-color: #151515; }
body.dark .aside .copyright-text,
body.dark .contact .contact-form .form-item .form-control,
body.dark .about .about-content .timeline,
body.dark .aside {
    background-color: #222222; }

body.dark .contact .contact-info-item h4,
body.dark .portfolio .portfolio-filter button,
body.dark .about .about-content .timeline .timeline-title,
body.dark .about .about-content h3.title,
body.dark  .about .about-content .skills .skill-item h5,
body.dark .about .about-content .personal-info .info-item p,
body.dark .about .about-content .about-text h3,
body.dark .section-title h2,
body.dark .home .intro h1,
body.dark .aside .logo a,
body.dark .aside .nav li a {
    color: #ffffff;
    border-color: #393939; }
body.dark .aside {}
body.dark .contact .contact-form .form-item .form-control,
body.dark .contact .contact-info-item p,
body.dark .portfolio .portfolio-item-inner,
body.dark .about .about-content .timeline .timeline-text,
body.dark .about .about-content .timeline .timeline-date,
body.dark .about .about-content .skills .skill-item .skill-percent,
body.dark .about .about-content .personal-info .info-item p span,
body.dark .about .about-content .about-text p,
body.dark .home .intro p {
    color: #e9e9e9;
 }    // Responsive

@media(max-width: 1199px) {
    .aside {
        left: -270px; }
    .main-content {
        padding-left: 0; }
    .about .about-content .personal-info .info-item p span {
        display: block;
        margin-left: 0; } }
@media(max-width: 991px) {
    .portfolio .portfolio-item {
        flex: 0 0 50%;
        max-width: 50%; } }
@media(max-width: 767px) {
    .about .about-content .experience,
    .about .about-content .education,
    .about .about-content .skills,
    .about .about-content .personal-info {
        flex: 0 0 100%;
        max-width: 100%; }
    .portfolio .portfolio-filter button {
        margin-bottom: 10px; } }
@media(max-width: 575px) {
    .contact .contact-info-item,
    .portfolio .portfolio-item {
        flex: 0 0 100%;
        max-width: 100%; } }
